.nav {
  position: fixed;
  top: 0;
  width: 100%;

  display: flex;
  justify-content: space-between;
  z-index: 1;
  transition-timing-function: ease-in;
  transition: all 0.5s;
  height: 30px;
}

.nav__logo {
  width: 50px;
  object-fit: contain;
}

.nav__black {
  background-color: #111;
}
